.root {
  font-family: "Montserrat", sans-serif;
}

.header {
  margin-top: 0;
  font-size: 20px;
  text-align: center;
}
.subHeader {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 8px;
}
.sizes {
  text-align: center;
}
.size {}
.price {
  display: flex;
  justify-content: center;
  font-weight: bold;
  text-align: center;

  > div {
    margin-top: 8px;
  }
}
.inputs {
  display: flex;
  flex-direction: column;
  align-items: center;

  .input {
    margin: 4px 0;

    input {
      padding: 8px;
      border: solid 1px gray;
      border-radius: 8px;
      outline: 0;
      font-family: inherit;
      transition: 0.2s;

      &:focus {
        border-color: brown;
      }
    }

    > div {
      font-weight: bold;
      margin-bottom: 4px;
    }

    &:last-child {
      margin-bottom: 12px;
    }
  }
}

.error {
  color: red;
  opacity: 0.8;
  text-align: center;
}

.placeholder {
  min-height: 60px;
}